body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
@font-face {
  font-family: 'GodoM'; src: url(./styles/fonts/GodoM.ttf) format('truetype'); 
}
.select-file-button{
  font-size: small;
  padding: 6px 25px;
  background-color:white;
  border-radius: 4px;
  color: #6f6f6f;
  cursor: pointer;
  margin: 5px;
}

.upload-file-button{
  font-size: small;
  padding: 6px 25px;
  background-color:#00a0fe;
  border-radius: 10px;
  color: #000000;
  cursor: pointer;
  margin: 5px;
  font-family: 'GodoM';
}



.wrap {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.button {
  width: 100px;
  height: 35px;
  font-family: 'GodoM';
  font-size: 11px;
  text-transform: uppercase;
  letter-spacing: 2.5px;
  font-weight: 500;
  color: #000;
  background-color: #fff;
  border: none;
  border-radius: 45px;
  box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease 0s;
  cursor: pointer;
  outline: none;
  }

.button:hover {
  background-color: #00a0fe;
  box-shadow: 0px 15px 20px rgba(30, 199, 255, 0.4);
  color: #fff;
  transform: translateY(-20px);
}


